body {
  margin: 0;
  font-family: 'Merriweather Sans', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: black;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/* NavBar.css */
nav {
  width: 100vw;
  display: flex;
  justify-content: space-between;
  background-color: #001219;
  align-items: center;
  position: -webkit-sticky;
  position: sticky;
  top: 0 ;
  z-index: 101;
}
nav h1 {
  color: #fff;
  margin-left: 40px;
}
nav a {
  text-decoration: none;
}
nav span {
  /* color: #ee9b00; */
  cursor: pointer;
  margin-left: 2.5rem;
  font-size: 0.85rem;
  text-transform: uppercase;
}
nav span:hover {
  color: #fff;
  border-bottom: 2px solid #ee9b00;
  padding-bottom: 5px;
}
span #HomeLight,
#MoviesLight,
#TrendingLight,
#PricingLight {
  color: #fff;
}
span #HomeLight:hover,
#MoviesLight:hover,
#TrendingLight:hover,
#PricingLight:hover {
  border-bottom: 2px solid #ff206e;
}
#navBarColor {
  background-color: #38bdab;
  transition: 0.6s;
}
.nav-options {
  display: flex;
  align-items: center;
}
#heading {
  color: #ff206e;
}
#navBarColor span:hover {
  border-bottom: 2px solid #ff206e;
  padding-bottom: 5px;
}
.input-group {
  display: flex;
  align-items: center;
}
#Color-switcher {
  position: relative;
  margin-right: 40px;
  background-color: #413e44;
  width: 70px;
  height: 30px;
  border-radius: 20px;
}
#Color-switcher-mover {
  position: absolute;
  left: 5px;
  top: 3px;
  background-color: #fff;
  width: 25px;
  height: 25px;
  border-radius: 50%;
  transition: 0.6s;
}
#Color-switcher-moved {
  position: absolute;
  background-color: #fff;
  left: 40px;
  top: 3px;
  width: 25px;
  height: 25px;
  border-radius: 50%;
  transition: 0.6s;
}
.input-group input {
  position: relative;
  width: 323px;
  height: 4vh;
  border-radius: 16px;
  border: none;
  margin-right: 30px;
}
.input-group input[type="text"] {
  padding-left: 17px;
  font-size: 1rem;
  color: black;
}
.input-group input::-webkit-input-placeholder {
  color: black;
}
.input-group input::placeholder {
  color: black;
}
.input-group input:focus {
  outline: none;
}
#search {
  position: absolute;
  margin-left: 310px;
}


/* Videos.css  */
body{
    overflow-x: hidden;
}
.movies-container{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}
.mainBgColor{
    background-color:#0c1e25;
    width: 100%;
}
.secondaryBgColor{
    background-color: #fff;
    width: 100%;
}
.movies-container img{
    width: 350px;
    height: 550px;
    margin: 25px 0px 4px 15px;
    border-radius: 18px;
}
.mainColor{
    color: #fff;
    display: flex;
    justify-content: center;
    font-size: 1rem;
}
#Nothing{
    display: none;
}
.secondaryColor{
    color: #ff206e;
    width: 90%;
    display: flex;
    justify-content: center;
    font-size: 1rem;
}
.movies-container h3, .movies-container img{
    cursor: pointer;
    transition: .7s;
}
.movies-container img:hover{
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
}
#container{
    position: relative;
}
#hide{
    display: none;
}
#smaller-Text{
    width: 100%;
    font-size: 14px;
    display: flex;
    justify-content: center;
}
#playIcon{
    position: absolute;
    bottom: 70px;
    left: 20px;
    visibility: visible;
    z-index: 100;
    cursor: pointer;
}
#Exit1{
    position: absolute;
    margin-left: 800px;
    margin-top: 67px;
    z-index: 999;
}
#Nothing{
    color: green;
}
#NoContainer{
    opacity: 0.35;
}
.DarkTheme{
    color:#fff;
}
.LightThemeClose{
    color: #ff206e;
}
/* TrailerMovi.css */
.player {
  position: absolute;
  margin-top: 120px;
  z-index: 100;
}
.Container {
  display: none;
  position: absolute;
  z-index: 100;

}
#TrailerMovie-name-dark {
  display: flex;
  width: 100%;
  justify-content: center;
  position: absolute;
  top: -100px;
  font-size: 2.5rem;
  color: #ee9b00;
}
#TrailerMovie-name-light {
  display: flex;
  width: 100%;
  justify-content: center;
  position: absolute;
  top: -100px;
  font-size: 2.5rem;
  color: #ff206e;
}

/* pricing.css */

.backgroun-Color-Main {
  width: 100vw;
  height: 91vh;
  background-color: black;
  /* padding-top: 20px; */
  display: flex;
  justify-content: center;
  align-items: center;
}
.backgroun-Color-secondary {
  width: 100vw;
  height: 91vh;
  background-color: #fff;
  /* padding-top: 20px; */
  display: flex;
  justify-content: center;
  align-items: center;
}
.Pricing-container {
  display: flex;
  color: #fff;
}
.Pricing-option1,
.Pricing-option2,
.Pricing-option3 {
  width: 30vw;
  height: 63vh;
  background-color: #000d13;
  margin-right: 10px;
  border-radius: 16px;
  background: green;
}
.light-Theme1,
.light-Theme2,
.light-Theme3 {
  width: 30vw;
  height: 63vh;
  background-color: #29534e;
  margin-right: 10px;
  border-radius: 16px;
}
.Pricing-option1 h3,
.Pricing-option2 h3,
.Pricing-option3 h3 {
  display: flex;
  justify-content: center;
  font-size: 2rem;
  color: #ee9b00;
}
.Price {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: row;
 
}
.light-Theme1 h3,
.light-Theme2 h3,
.light-Theme3 h3 {
  font-size: 2rem;
  display: flex;
  justify-content: center;
  color: #fff;
}
.Pricing-option1 h2,
.Pricing-option2 h2,
.Pricing-option3 h2 {
  font-size: 2.4rem;
  margin-bottom: 0.2rem;
  display: flex;
  justify-content: center;
}
.light-Theme1 h2,
.light-Theme2 h2,
.light-Theme3 h2 {
  color: #ff206e;
  font-size: 2.4rem;
  margin-bottom: 0.2rem;
  display: flex;
  justify-content: center;
}
.Pricing-option1 span,
.Pricing-option2 span,
.Pricing-option3 span {
  display: flex;
  font-size: 1.3rem;
  margin-left: 2.5rem;
  margin-bottom: 15px;
  font-weight: 18rem;
}
#MonthlyYearly {
  font-size: 1.4rem;
}
.light-Theme1 span,
.light-Theme2 span,
.light-Theme3 span {
  display: flex;
  font-size: 1.3rem;
  margin-left: 2.5rem;
  margin-bottom: 15px;
  font-weight: 18rem;
  color: #fff;
}
#checkmark {
  margin-right: 0.5rem;
}
button {
  width: 60%;
  height: 6vh;
  margin-left: 5rem;
  border: none;
  border-radius: 0.4rem;
  background-color: #659664;
  color: #fff;
  font-size: 1.3rem;
  cursor: pointer;
}
button:hover {
  background-color: #588157;
  transition: 0.4s;
}
#button1 {
  margin-top: 7.5rem;
}
#button2 {
  margin-top: 4.93rem;
}
#button3 {
  margin-top: 2.3rem;
}
#darktheme {
  display: flex;
  justify-content: flex-end;
}
.Pricing-yearly-darktheme {
  position: relative;
  margin-left: 250px;
  width: 70px;
  height: 30px;
  border-radius: 20px;
  background-color: #413e44;
  margin-right: 10px;
}
.Pricing-monthly-darktheme {
  position: absolute;
  left: 5px;
  top: 3px;
  background-color: #fff;
  width: 25px;
  height: 25px;
  border-radius: 50%;
  transition: 0.6s;
}
.Pricing-monthly-light {
  position: absolute;
  background-color: #fff;
  left: 40px;
  top: 3px;
  width: 25px;
  height: 25px;
  border-radius: 50%;
  transition: 0.6s;
}

